import StaticMap from 'vue-static-map';

export default {
  name: 'Location',
  props: ['latitude', 'longitude', 'address'],
  data() {
    return {
      center: `${this.latitude},${this.longitude}`,
      markers: [
        {
          color: 'blue',
          lat: this.latitude,
          lng: this.longitude,
          size: 'normal'
        }
      ],
      type: 'roadmap',
      size: [240, 153]
    };
  },
  components: {
    StaticMap
  },
  computed: {},
  mounted() {},
  methods: {}
};



