import { mapActions } from 'vuex';
import Modal from '@/components/Components/Modal/index.vue';

export default {
  name: 'RoomCreateModal',
  props: ['invitee'],
  data() {
    return {
      roomData: {
        _id: null,
        name: null,
        description: null,
        cover: null,
        invitee: null
      }
    };
  },
  components: {
    Modal
  },
  mounted() {
    if (this.invitee) {
      this.roomData.invitee = this.invitee;
    }
  },
  methods: {
    async create() {
      let request = {};
      for (let key in this.roomData) {
        if (this.roomData[key]) {
          request[key] = this.roomData[key];
        }
      }

      request.invitee = request.invitee.replace(/\s/, '').split(',');
      if (request.invitee.length === 1) {
        request.invitee = request.invitee[0];
      }

      await this.createAndJoinRoom(request);

      this.$emit('close');
    },
    cancel() {
      this.$emit('close');
    },
    ...mapActions(['createAndJoinRoom'])
  }
};



