import { mapActions, mapGetters } from 'vuex';
import lib from '@/lib';
import Scrollbar from '@/components/Components/Scrollbar/index.vue';
import RoomChatOutputMessage from '@/components/RoomChat/RoomChatOutput/RoomChatOutputMessage/index.vue';
import RoomChatOutputSilder from '@/components/RoomChat/RoomChatOutput/RoomChatOutputSilder/index.vue';

export default {
  name: 'RoomChatOutput',
  data() {
    return {
      newMessage: null,
      slider: {
        show: false,
        default: ''
      }
    };
  },
  components: {
    Scrollbar,
    RoomChatOutputMessage,
    RoomChatOutputSilder
  },
  computed: {
    messages() {
      if (this.roomChat) {
        return this.roomChat.messages;
      } else {
        return null;
      }
    },
    isMobile() {
      try {
        document.createEvent('TouchEvent');
        return true;
      } catch (e) {
        return false;
      }
    },
    ...mapGetters(['roomChat'])
  },
  mounted() {
    this.$store.subscribe((mutation, state) => {
      switch (mutation.type) {
        // 重新載入所有聊天：
        // 卷軸滾到最下面
        case 'setRoomChatListItem':
          this.$nextTick(() => {
            if (
              this.$refs.systemUnread &&
              (this.$refs.systemUnread.message ||
                this.$refs.systemUnread.length)
            ) {
              let systemUnread =
                this.$refs.systemUnread[0] || this.$refs.systemUnread;
              this.$refs.list.scrollElement(systemUnread.$el);
              return;
            }
            this.$refs.list.scrollBottom(0);
          });
          break;
        // 往上載入更多
        // 先記錄 scrollBottom，載入完移回來
        case 'prependRoomChatListItemMessages':
          let prependMessagesScrollBottom = this.$refs.list.scrollBottom();
          this.$nextTick(() => {
            this.$refs.list.scrollBottom(prependMessagesScrollBottom);
          });
          break;
        case 'appendRoomChatListItemMessage':
          let appendMessageScrollBottom = this.$refs.list.scrollBottom();
          this.$nextTick(async () => {
            if (appendMessageScrollBottom < 10) {
              this.$refs.list.scrollBottom(0);
              await lib.message.textToUrlType(mutation.payload);
              this.$refs.list.scrollBottom(0);
            } else {
              lib.message.textToUrlType(mutation.payload);
              // 如果送訊息的不是自己，提示有新訊息
              if (
                mutation.payload.sender &&
                mutation.payload.sender.id !== this.$store.state.client.client
              ) {
                this.newMessage = mutation.payload;
              }
            }
          });
          break;
      }
    });
  },
  methods: {
    resize() {},
    onListScroll() {
      // 往上滑到頂
      if (
        this.$refs.list.hasScroll &&
        this.$refs.list.scrollTop() < 50 &&
        this.$refs.list.scrollTop() > 0 && // 避免一載入就觸發
        !this.roomChat.hasBegin &&
        !this.roomChat.isLoadingBefore
      ) {
        this.getRoomMessagesBefore();
      }

      // 往下滑到底
      if (this.$refs.list.scrollBottom() < 10) {
        this.newMessage = null;
      }
    },
    scrollToBottom() {
      this.$refs.list.scrollBottom(0);
    },
    showSilder(message) {
      this.slider.default = message.id;
      this.slider.show = true;
    },
    closeSilder() {
      this.slider.show = false;
    },
    setReply(message) {
      this.$emit('setReply', message);
    },
    gotoMessage(messageId) {
      let messageElement = document.getElementById(`message-${messageId}`);
      if (messageElement) {
        this.$refs.list.scrollElement(messageElement);
        if (messageElement.classList) {
          messageElement.classList.add('shake');
          setTimeout(() => {
            messageElement.classList.remove('shake');
          }, 2000);
        }
      }
    },
    ...mapActions(['getRoomMessagesBefore'])
  }
};



