import Avatar from '@/components/Components/Avatar/index.vue';
import Checkbox from '@/components/Components/Checkbox/index.vue';

export default {
  name: 'RoomListItem',
  props: [
    'room',
    'active',
    'isShowTime',
    'isShowUnread',
    'isShowCheckbox',
    'checked'
  ],
  data() {
    return {};
  },
  components: {
    Avatar,
    Checkbox
  },
  methods: {
    roomClick() {
      this.$emit('roomClick');
    }
  }
};



