class Room {
  // 補足缺少的 key，缺少的 key 會導致資料更新時無法同步渲染
  fullKeys(room) {
    let keys = ['name', 'cover', 'description', 'lastMessage'];
    for (let key of keys) {
      if (!(key in room)) {
        room[key] = null;
      }
    }
    room.unread = room.unread || 0;
  }
}

export default Room;



